.heading {
  font-size: 48px;
  line-height: 48px;
  position: absolute;
  text-transform: uppercase;
  width: max-content;
}

.heading__crown {
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  width: 23px;
  height: 16px;
  top: calc(100% + 7px);
  margin: auto;
  background-image: url(../images/crown.svg);
  background-repeat: no-repeat;
}
